.top {
    padding: 0.3em 0.5em;
    display: flex;
}

.top img {
    border-radius: 100%;
    width: 2.4em;
    height: 2.4em;
    margin: auto 0;
}

.top p {
    margin-left: 0.6em;
}

.photo img {
    width: 100%;
}

.activities {
    margin-left: 1em;
}

.buttons button {
    margin-right: 0.5em;
}

.likes p {
    margin: 0.3em 0em;
}

.time {
    margin-top: 0.5em;
    text-transform: uppercase;
    color: grey;
}